html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

@font-face {
  font-family: 'Open Sans';
  src: url("../../assets/fonts/OpenSans-BoldItalic.woff2") format("woff2"), url("../../assets/fonts/OpenSans-BoldItalic.woff") format("woff");
  font-weight: bold;
  font-style: italic; }

@font-face {
  font-family: 'Open Sans';
  src: url("../../assets/fonts/OpenSans-Bold.woff2") format("woff2"), url("../../assets/fonts/OpenSans-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: 'Open Sans';
  src: url("../../assets/fonts/OpenSansLight-Italic.woff2") format("woff2"), url("../../assets/fonts/OpenSansLight-Italic.woff") format("woff");
  font-weight: 300;
  font-style: italic; }

@font-face {
  font-family: 'Open Sans';
  src: url("../../assets/fonts/OpenSans-Light.woff2") format("woff2"), url("../../assets/fonts/OpenSans-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal; }

html {
  background-color: #000; }

body {
  color: #ffffff;
  font-size: 16px;
  font-family: 'Open Sans', Helvetica, sans-serif;
  font-weight: 300;
  font-style: normal;
  line-height: 1.5rem; }

.select-container {
  margin: 0 auto;
  text-align: center;
  line-height: 1.5rem; }

.select-text {
  padding-bottom: 1rem; }

.logo {
  padding: 100px; }

#selection {
  font-size: 16px;
  width: 120px; }

.container {
  width: 1240px; }

.block-container {
  width: 600px;
  height: 100px;
  margin: 0; }

.stories-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(4, 1fr);
  /* background:linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3)), no-repeat; */
  /* padding-top: 300px; */
  /* height:100px;
    margin-top:60px;
    margin-bottom:100px; */ }

footer {
  text-align: center;
  padding: 60px; }

/*---------------------------- Media Queries --------------------------------*/
/*--------------------------- Tablet --------------------------------*/
@media screen and (min-width: 600px) {
  .select-container {
    display: flex;
    flex-direction: row;
    text-align: center;
    width: 600px;
    align-items: center;
    padding: 5.5rem;
    align-content: center; }
  .logo {
    padding: 0px; }
  .select-text {
    padding: 3rem; } }

/*---------------------------- Desktop ------------------------------*/
@media screen and (min-width: 1000px) {
  .select-container {
    display: flex;
    flex-direction: row;
    text-align: center;
    width: 1240px;
    align-items: center;
    padding-top: 5rem;
    padding-left: 0px; }
  .logo {
    padding: 0px; }
  .select-text {
    padding: 5rem; }
  footer {
    text-align: left;
    padding: 3rem; } }
